<template>
  <el-button :icon="type==='back'?'el-icon-arrow-left':''" @click="handleClick" type="primary" size="small" plain>
    <slot></slot>
    <i v-if="type==='next'" class="el-icon-arrow-right el-icon--right"></i>
  </el-button>
</template>

<script>
export default {
  name: 'BackButton',
  props: {
    type: {
      type: String,
      default: 'back'
    }
  },
  methods: {
    handleClick () {
      if (this.type === 'back') {
        this.back()
      } else {
        this.$emit('next')
      }
    },
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>

</style>



